import {Component, OnInit} from "@angular/core";
import {RefreshHandler} from "@service/global-refresh.service";
import {ClusterPowerService} from "@generated/cluster-power-service";
import {ManagedObject} from "@service/managed-object";
import {ClusterPowerStateUtils} from "@util/cluster-power-state-utils";
import {ClusterPowerState} from "@generated/cluster-power-state";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {Loader} from "@service/flow/loader.decorator";
import {ErrorHandler} from "@service/flow/error-handler.decorator";

@Component({
   templateUrl: "summary-portlet.html",
   selector: "vsan-cluster-summary",
   styleUrls: ["summary-portlet.scss"],
})
export class ClusterSummaryPortletComponent implements OnInit {

   clusterRef: ManagedObject;
   hasEditClusterPermission: boolean = false;

   alertMessages: string[] = [];

   /**
    * It's only used for the initial loading because the children components do the global refresh handling internally.
    */
   isInitiallyLoaded: boolean = false;

   isClusterPoweredOff: boolean;
   unavailableViewSpec: UnavailableViewSpec = UnavailableViewSpec.SERVICES_VIEW_CLUSTER_SHUT_DOWN;

   constructor(private clusterPowerService: ClusterPowerService, private permissionService: PermissionService) {}

   async ngOnInit(): Promise<void> {
      this.clusterRef = ManagedObject.contextObject;
      await Promise.all([
            this.checkForEditClusterPermission(),
            this.fetch(),
      ]);
      this.isInitiallyLoaded = true;
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      this.alertMessages = [];
      this.isClusterPoweredOff = ClusterPowerStateUtils.isClusterPoweredOff(await this.queryClusterPowerState());
   }

   async checkForEditClusterPermission(): Promise<void> {
      this.hasEditClusterPermission = await this.permissionService.hasPermissions(
            this.clusterRef, [Privileges.EDIT_CLUSTER()]);
   }

   /**
    * Decorate only the API call, to have both the error handling and to be sure that we will update the
    * cluster power state no matter what the result is gonna be.
    */
   @Loader()
   async queryClusterPowerState(): Promise<ClusterPowerState> {
      return this.clusterPowerService.getPowerState(this.clusterRef);
   }

   /**
    * Collect errors from this and the children components
    */
   @ErrorHandler()
   handleDataFailed(error: string): void {
      this.alertMessages.push(error);
   }
}
