import {VsanHealthUtil} from "@util/vsan-health-util";
import {ChangeDetectorRef, Component, EventEmitter, OnInit, Output, ViewRef} from "@angular/core";
import {RefreshHandler} from "@service/global-refresh.service";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VsanTestData} from "@generated/vsan-test-data";
import {VsanHealthData} from "@generated/vsan-health-data";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ReferenceWatcher} from "@util/reference-watcher";

@Component({
   selector: "vsan-cluster-summary-health",
   templateUrl: "cluster-summary-health.html",
   styleUrls: ["cluster-summary-health.scss"],
   providers: [ReferenceWatcher],
})
export class ClusterSummaryHealthComponent implements OnInit {

   loading: boolean = false;

   healthStatus: VsanHealthStatus = null;

   statusLabel: string = "";

   @Output()
   getHealthDataFailed: EventEmitter<string> = new EventEmitter<string>();

   constructor(private healthProvider: VsanHealthPropertyProvider,
         private navigationService: NavigationService,
         private refWatcher: ReferenceWatcher,
         private changeDetector: ChangeDetectorRef) {
      this.refWatcher.watchGlobalRefreshSubscription(ClusterSummaryHealthComponent.prototype.constructor.name);
   }

   ngOnInit(): void {
      this.fetch();
   }

   @RefreshHandler
   fetch(): void {
      this.loading = true;
      this.healthStatus = null;
      this.statusLabel = "";
      this.healthProvider.getVsanHealthSummaryFromCache(ManagedObject.contextObject)
            .then(this.handleHealthData)
            .catch(this.onCatch);
      this.detectChanges();
   }

   private handleHealthData = (result: VsanHealthData): void => {
      this.loading = false;
      if (!result || !result.testsData || !result.testsData.length) {
         this.detectChanges();
         Logger.warn("The health summary data from cache is empty.");
         return;
      }
      this.buildStatus(result.testsData);
   };

   /**
    * In the case of red status tests, an error status icon and "x issues" label are displayed,
    * In the absence of red but yellow, a warning status icon and "x warnings" label are displayed,
    * When there are no red or yellow status tests show a green status icon and "OK" label.
    */
   private buildStatus = (testsData: VsanTestData[]) => {
      let redTests: number = 0;
      let yellowTests: number = 0;
      testsData.map(test => {
         const subtests: VsanTestData[] = test.subtests;
         subtests.map(subtest => {
            if (subtest.status === VsanHealthStatus.red) {
               redTests += 1;
            }
            if (subtest.status === VsanHealthStatus.yellow) {
               yellowTests += 1;
            }
         });
      });

      if (redTests > 0) {
         this.healthStatus = VsanHealthStatus.red;
         this.statusLabel = redTests === 1
               ? VsanUiUtils.getString("vsan.cluster.health.summary.errors.label.singular", redTests)
               : VsanUiUtils.getString("vsan.cluster.health.summary.errors.label.plural", redTests);
         this.detectChanges();
         return;
      }
      if (yellowTests > 0) {
         this.healthStatus = VsanHealthStatus.yellow;
         this.statusLabel = yellowTests === 1
               ? VsanUiUtils.getString("vsan.cluster.health.summary.warnings.label.singular", yellowTests)
               : VsanUiUtils.getString("vsan.cluster.health.summary.warnings.label.plural", yellowTests);
         this.detectChanges();
         return;
      }
      this.healthStatus = VsanHealthStatus.green;
      this.statusLabel = VsanUiUtils.getString("vsan.cluster.health.summary.ok.label");
      this.detectChanges();
   };

   private onCatch = (error: any): void => {
      this.getHealthDataFailed.emit(VsanUiUtils.getString("vsan.cluster.health.summary.errors.message"));
      this.loading = false;
      Logger.log("Unable to query health summary data from cache: " + error);
      this.detectChanges();
   };

   onDetailsClick = (): void => {
      this.navigationService.navigateTo(PlatformExtension.CLUSTER_HEALTH, ManagedObject.contextObject);
   };

   get statusClass(): string {
      return VsanHealthUtil.getVsanHealthStatusIconClass(this.healthStatus);
   }

   get statusShape(): string {
      return VsanHealthUtil.getVsanHealthStatusIconShape(this.healthStatus);
   }

   /**
    * If the component is being marked as destroyed but we try to detect changes angular will throw exception
    */
   private detectChanges(): void {
      if (!(this.changeDetector as ViewRef).destroyed) {
         this.changeDetector.detectChanges();
      }
   }
}
