import {Component, EventEmitter, OnInit, Output} from "@angular/core";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {GraphMetric} from "@component/chart/performance/graph-metrics.data";
import {FlatGraphMetric} from "@component/chart/performance/flat-graph-metric";
import {PerfGraph} from "@component/chart/performance/perf-graph";
import {PerfChartUtil} from "@component/chart/performance/perf-chart-util";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {VsanClusterSummaryService} from "@generated/vsan-cluster-summary-service";
import {SummaryPerformanceData} from "@generated/summary-performance-data";
import {PefGraphId} from "@component/chart/model/perf-graph-id";
import {ChartPlotLineSelectionData} from "@component/chart/model/chart-plot-line-selection.data";
import {VsanUiUtils} from "@util/vsan-util";
import {TopContributorsContext} from "@navigation/context/top-contributors-context";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {PerfTimeRangeData} from "@generated/perf-time-range-data";
import {ReferenceWatcher} from "@util/reference-watcher";

@Component({
   selector: "vsan-cluster-summary-performance",
   templateUrl: "cluster-summary-performance.html",
   styleUrls: ["cluster-summary-performance.scss"],
   providers: [ReferenceWatcher],
})
export class ClusterSummaryPerformanceComponent implements OnInit {
   loading: boolean;
   perfServiceEnabled: boolean;
   perfServiceHealthy: boolean;
   isTopContributorsSupported: boolean;
   hasChartData: boolean;
   performanceUnavailableLabel: string;
   tooltipActionLink: string;
   flatData: FlatGraphMetric[] = [];

   @Output()
   getPerformanceDataFailed: EventEmitter<string> = new EventEmitter<string>();

   constructor(private navigationService: NavigationService,
         private summaryService: VsanClusterSummaryService,
         private refWatcher: ReferenceWatcher) {
      this.refWatcher.watchGlobalRefreshSubscription(ClusterSummaryPerformanceComponent.prototype.constructor.name);
   }

   ngOnInit(): void {
      this.fetch();
   }

   @RefreshHandler
   fetch(): void {
      this.loading = true;
      this.summaryService.getSummaryPerformanceData(ManagedObject.contextObject)
            .then((result: SummaryPerformanceData) => {
               this.loading = false;
               this.perfServiceEnabled = result.isPerfEnabled;
               this.perfServiceHealthy = result.isPerfServiceHealthy;
               this.isTopContributorsSupported = result.isTopContributorsSupported;

               this.setPerformanceUnavailableLabel();
               this.tooltipActionLink = this.isTopContributorsSupported ?
                     VsanUiUtils.getString("vsan.monitor.cluster.performance.topContributors.tooltip.action.link") : null;

               if (this.perfServiceEnabled) {
                  this.handleChartsData(result);
               }
            }).catch(this.handleError);
   }

   setPerformanceUnavailableLabel(): void {
      if (!this.perfServiceEnabled) {
         this.performanceUnavailableLabel = VsanUiUtils.getString("vsan.cluster.performance.summary.disabled.label");
      } else if (!this.perfServiceHealthy) {
         this.performanceUnavailableLabel = VsanUiUtils.getString("vsan.monitor.cluster.performance.unhealthy.description");
      }
   }

   private handleChartsData(perfData: SummaryPerformanceData): void {
      this.hasChartData = !!perfData.chartsData;
      // There was no chart data when the performance service was just enabled.
      if (!this.hasChartData) {
         return;
      }

      let graphs: PerfGraph[] = [];
      if (perfData && perfData.clusterDomClientEntity && perfData.clusterDomClientEntity.graphs) {
         graphs = perfData.clusterDomClientEntity.graphs
               .filter(graph => Object.values(PefGraphId).includes(graph.id))
               .map(graph => PerfChartUtil.getGraphDefinition(graph));
      }

      this.flatData = [];
      const graphsData: GraphMetric[] = [new GraphMetric(perfData.chartsData, graphs)];
      graphsData.forEach((entry: GraphMetric) => {
         entry.graphs.forEach(graph => {
            this.flatData.push(new FlatGraphMetric(entry.entityRefId, entry.metrics, graph));
         });
      });
   }

   private handleError = (error: string) => {
      this.loading = false;
      this.getPerformanceDataFailed.emit(error);
   };

   onDetailsClick = () => {
      if (!this.perfServiceEnabled || !this.perfServiceHealthy) {
         this.navigationService.navigateTo(PlatformExtension.CLUSTER_SERVICES);
      } else {
         this.navigationService.navigateTo(PlatformExtension.CLUSTER_PERFORMANCE);
      }
   };

   tooltipActionLinkClicked = (data: ChartPlotLineSelectionData) => {
      const now: number = VsanDateTimeUtils.roundToMinuteStart(new Date().getTime());
      // sets the start time 2 hours ago, because the perf. data is the chart is for the last two hours too
      const startTime: number = now - VsanDateTimeUtils.HOUR_IN_MILLISECONDS * SummaryPerformanceData.LAST_TWO_HOURS();
      const timePickerRange: PerfTimeRangeData = PerfTimeRangeData.Factory.create(
            "", startTime, now, null);
      const props: TopContributorsContext = {
         selectedTimePoint: data.timePoint,
         selectedGraphId: data.graphId,
         timePickerRange: timePickerRange,
      };

      this.navigationService.navigateTo(PlatformExtension.CLUSTER_PERFORMANCE, null, props);
   };
}